import React, {useState} from 'react';
import { IoSend } from "react-icons/io5";
import useSendMessage from '../../hooks/useSendMessage';

const MessageInput = () => {
    const [message, setMessage] = useState('');
    const {sendMessage, loadingState} = useSendMessage();

    const handleSubmit = async (e) => {
        e.preventDefault();

        console.log(message);

        if(message){
            await sendMessage(message);
            setMessage('');
        }
    };

    return(
        <form className='px-4 my-3' onSubmit={handleSubmit}>
            <div className='w-full relative'>
                <input type="text" className='border text-sm rounded-lg block w-full p-2.5 bg-gray-700 border-gray-600 text-white' 
                    placeholder='Send a message'
                    value={message}
                    onChange={(e) => {return setMessage(e.target.value)}}/>

                {loadingState ? (<span className='loading loading-spinner'></span>) : 
                    <button type='submit' className='absolute inset-y-0 end-0 flex items-center pe-3'>
                        <IoSend></IoSend>
                    </button>}
            </div>
        </form>
    )
};

export default MessageInput;



